<template>
  <b-container fluid id="signup">
    <b-row class="justify-content-center mt-5"
      ><img src="@/assets/logo.png"
    /></b-row>
    <b-row class="justify-content-center mt-5"
      ><h5 class="mt-4" style="color: gray">
        로그인 하셔야 서비스를 이용할 수 있습니다
      </h5></b-row
    >
    <b-row style="margin-top: 2%">
      <b-col cols="6">
        <b-col style="margin-left: 3%">
          <img src="@/assets/login.jpg" width="120%" style="opacity: 0.4"
        /></b-col>
      </b-col>
      <b-col cols="4" class="justify-content-center" style="margin-top: 2%">
        <div>
          <h1>
            <strong
              ><img
                src="data:image/png;base64,"
              />
              &nbsp; 로그인
            </strong>
          </h1>
        </div>
        <div class="card card-container mt-4">
          <form name="form" @submit.prevent="handleLogin" class="m-5">
            <div class="form-group">
              <label for="username"><h4>아이디</h4></label>
              <b-input
                v-model="user.username"
                v-validate="'required'"
                type="text"
                class="form-control"
                name="username"
                size="lg"
              />
              <div
                v-if="errors.has('username')"
                class="alert alert-danger"
                role="alert"
              >
                Username is required!
              </div>
            </div>
            <div class="form-group mt-5">
              <label for="password"><h4>비밀번호</h4></label>
              <b-input
                v-model="user.password"
                v-validate="'required'"
                type="password"
                class="form-control"
                name="password"
                size="lg"
              />
              <div
                v-if="errors.has('password')"
                class="alert alert-danger"
                role="alert"
              >
                Password is required!
              </div>
            </div>
            <div class="form-group mt-5">
              <button
                class="btn btn-dark btn-block"
                pill
                size="lg"
                :disabled="loading"
              >
                <span
                  v-show="loading"
                  class="spinner-border spinner-border-sm"
                ></span>
                <span><h4>로그인</h4></span>
              </button>
              <button
                class="btn btn-block mt-4"
                pill
                size="lg"
                @click="$router.push('/user/signup')"
              >
                <h4>회원가입</h4>
              </button>
            </div>
            <div class="form-group">
              <div v-if="message" class="alert alert-danger" role="alert">
                {{ message }}
              </div>
            </div>
          </form>
        </div>
        <h6
          class="mt-5 text-center"
          style="font-family: ROKAFSlabSerifMedium; color: gray"
        >
          비밀번호가 기억나지 않는다면 관리자에게 메일을 보내주세요
          [admin@ssafy.com]
        </h6>
        <h6 class="mt-5 text-center">
          <a
            href="https://www.freepik.com/vectors/access-control"
            style="color: #ebebe8; font-style: italic"
            >Image copyright: Access control vector created by vectorjuice -
            www.freepik.com</a
          >
        </h6>
      </b-col>
    </b-row>
  </b-container>
</template>

<script>
import User from "../../models/user";

export default {
  name: "UserLogin",
  data() {
    return {
      user: new User("", ""),
      loading: false,
      message: "",
    };
  },
  computed: {
    loggedIn() {
      return this.$store.state.auth.status.loggedIn;
    },
  },
  created() {
    if (this.loggedIn) {
      this.$router.push("/");
    }
  },
  methods: {
    handleLogin() {
      this.loading = true;
      this.$validator.validateAll().then((isValid) => {
        if (!isValid) {
          this.loading = false;
          return;
        }

        if (this.user.username && this.user.password) {
          this.$store.dispatch("auth/login", this.user).then(
            () => {
              this.$router.push("/");
            },
            (error) => {
              this.loading = false;
              this.message =
                (error.response &&
                  error.response.data &&
                  error.response.data.message) ||
                error.message ||
                error.toString();
            }
          );
        }
      });
    },
  },
};
</script>

